<template>
  <div class="v-multiselect" v-click-outside="hide">
    <div
      class="v-multiselect__select"
      :class="{ 'v-multiselect__select--error': showError }"
    >
      <div class="v-multiselect__select-wrap" @click="toggle">
        <input
          type="text"
          :id="id"
          :placeholder="placeholder"
          :value="getSelected"
          readonly
        />
        <div
          class="v-multiselect__icon"
          :class="{ 'v-multiselect__icon--active': show }"
        >
          <svg>
            <use href="@/assets/img/sprite.svg#icon-arrow-down-select"></use>
          </svg>
        </div>
      </div>
      <label :for="id" v-show="!showError">
        {{ label }}
        <span class="v-multiselect__require" v-show="requireInput">*</span>
      </label>
      <label :for="id" v-show="showError">{{ errorText }}</label>
    </div>
    <div class="v-multiselect__options" v-show="show">
      <div
        class="v-multiselect__options-item"
        v-for="(item, index) in options"
        :key="index"
      >
        <input
          type="checkbox"
          :id="`option${id}${index}`"
          :value="item"
          v-model="selected"
          @change="$emit('multi-change', selected)"
        />
        <label :for="`option${id}${index}`">{{ item[labelField] }}</label>
      </div>
    </div>
  </div>
</template>

<script>
import ClickOutside from "vue-click-outside";

export default {
  name: "vMultiselect",
  props: {
    label: {
      type: String,
      default: ""
    },
    options: {
      type: Array,
      default: () => []
    },
    labelField: {
      type: String,
      default: "title"
    },
    value: {
      type: Array,
      default: () => []
    },
    placeholder: {
      type: String,
      default: ""
    },
    showError: {
      type: Boolean,
      default: false
    },
    errorText: {
      type: String,
      default: ""
    },
    requireInput: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      id: null,
      show: false,
      selected: []
    };
  },
  methods: {
    toggle() {
      this.show = !this.show;
    },
    hide() {
      this.show = false;
    }
  },
  computed: {
    getSelected() {
      return this.selected.map(item => item[this.labelField]).join(", ");
    }
  },
  mounted() {
    this.id = this._uid;
  },
  watch: {
    value(val) {
      this.selected = val;
    }
  },
  directives: {
    ClickOutside
  }
};
</script>
